@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap");
* {
  margin: 0%;
  box-sizing: border-box;
}
body {
  font-family: "Roboto", sans-serif;
}
.container {
  padding: 1rem 0;
  background-image: url(https://images.pexels.com/photos/1431822/pexels-photo-1431822.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940);
  min-height: 100vh;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center;
}
.input-group {
  margin: 2rem 0;
  display: flex;
  justify-content: center;
}
.input-group input {
  width: 300px;
  height: 2rem;
  padding: 2rem;
  border: 2px solid grey;
  border-radius: 50px 0 50px 0;
  outline: 0;
  font-size: 1.5rem;
  background-color: rgba(252, 248, 248, 0.521);
  transition: 0.5s ease;
}
.input-group input:focus,
.input-group input:hover {
  border-color: black;
  background-color: #fff;
}
.weather-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.weather-content h1 {
  color: white;
  text-shadow: 3px 3px 5px black;
  font-size: 3rem;
  font-weight: 400;
}
.weather-content .weather-group {
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.weather-box {
  padding: 2rem;
  background-color: rgba(128, 128, 128, 0.4);
  margin: 2rem 0;
  text-align: center;
  border: 1px solid black;
  border-radius: 1rem;
  transition: 0.5s ease;
  cursor: pointer;
}
.weather-box:hover {
  background-color: rgb(32, 32, 32);
}
.regular-font {
  font-weight: 300;
  font-size: 18px;
  text-shadow: 1px 1px black;
  color: white;
  margin: 10px 0;
}
.temp {
  font-size: 3rem;
  color: white;
  text-shadow: 3px 4px 7px black;
}
